<template>
  <div class="staticReports">
    <el-container>
      <el-header class="minWidth">
        <div class="staticReports minWidth loadingtext"
            v-loading="imgLoading"
            element-loading-text="加载中..."
            element-loading-background="rgba(255, 255, 255, 0.8)">
          <el-row class="staticReports-header">
            <el-col :span="17"><span class="mz" @click="$router.back(-1)">流程报表</span> <i class="el-icon-arrow-down"></i>
            </el-col>
            <el-col :span="7" class="statisticsMiddle">
              <el-button @click="search" type="primary" size="mini" round>搜索</el-button>
              <el-button @click="reset" type="primary"  size="mini"  round>重置</el-button>
            </el-col>
          </el-row>
          <div class="taticReports-content">
           <!-- <div class="resulter">报表条件
              <div class="fl fz12 curpoint" @click="showToggle"><span v-text="arrt"></span> <i :class="icon"></i></div>
            </div>-->
              <div class="statement" v-show="isShow">
                <el-row>
                  <span class="fz16">静态-统计有效流程/模板数量</span>
                </el-row>
                <el-row class="staticReports-middle">
                  <el-col :sm="3" :md="1" class="classification-select"><span>流程分类选择</span></el-col>
                  <el-col :span="6" class="inputsuhu">
                     <span @dblclick="openDailog('processClassification')">
                          <i class="iconfont icon-organization pl5" @click="openDailog('processClassification')"></i>
                          <el-input  disabled  v-model="processCategoryName"  class="input-text" :title="processCategoryName"></el-input>
                      </span>
                  </el-col>
                  <!--<el-col :span="1"><i class="iconfont icon-organization pl5" @click="openDailog('processClassification')"></i></el-col>-->
                  <el-col :sm="2" :md="1" :span="4" class="process-select"><span>流程选择</span></el-col>
                  <el-col :span="6" class="inputsuhu">
                     <span @dblclick="openDailog('processSelection')">
                       <i class="iconfont icon-organization pl5" @click="openDailog('processSelection')">
                  </i>
                          <el-input  disabled  v-model="processName"  class="input-text" :title="processName"></el-input>
                      </span>
                  </el-col>

                </el-row>
                <el-row>
                  <el-col :span="4"  class="buttonWrap">
                    <el-button @click="exportList" class="upload" round type="primary" icon="iconfont icon-export" size="mini">导出
                    </el-button>
                  </el-col>
                </el-row>
              </div>
          </div>

          <div class="taticReports-content">
            <!--<div class="resulter" @click="showToggleTwo">报表结果-->
            <!--<div class="fl"><span v-text="arrtTwo"></span> <i :class="iconTwo"></i></div>-->
            <!--</div>-->
            <!--<hr>-->
            <div class="statement">
              <el-table
                ref="singleTable"
                :data="tableData"
                highlight-current-row
                border
                :height="isShow?fullHeight-305:fullHeight-285"
                class="table">
                <el-table-column
                  prop="company"
                  label="公司"
                  width="450">
                </el-table-column>
                <el-table-column
                  prop="processCnt"
                  label="流程数量">
                </el-table-column>
                <el-table-column
                  prop="templateCnt"
                  label="模板总数">
                </el-table-column>
                <el-table-column
                  prop="templateAvg"
                  label="流程平均模板数">
                </el-table-column>
                <el-table-column
                  prop="stepAvg"
                  label="模板平均步骤数">
                </el-table-column>
                <div slot="empty" v-show="!imgLoading">
                  <p :style="{'marginTop': '23px'}">暂无数据</p>
                </div>
              </el-table>
            </div>
          </div>
        </div>
        <!--弹窗组件-->
        <component :is="componentParam.componentType" v-if="showdialog"
                   @hideComponent="hideComponent" v-on:sureSubmit="showMsgfromchild"
                   v-bind:componentParam="componentParam"></component>

      </el-header>


    </el-container>

  </div>
</template>

<script>

  import processClassification from '../components/dailog/processClassification.vue'
  import processSelection from '../components/dailog/processSelection.vue'
  import util from '../js/util'
  import loading from '../plugins/loading/loading';
  import page from "../plugins/page/index";

  export default {
    name: 'staticReports',
    mounted() {
      //this.getList()
    },
    components: {
      'processClassification': processClassification, //选择流程分类
      'processSelection':processSelection  , //选择流程
      'page': page
    },
    data() {
      return {
        fullHeight: document.documentElement.clientHeight,
        model: {
          selectForm: {},
          companys: ''
        },
        componentParam: {
          //弹窗类型
          componentType:{
            type: String,
            required: true
          },
          //需要 回填到组件上的 数据集合
          backfillData:{
            type: Array,
            required: true
          },
          //子组件是否单选(true单选，false不是单选)
          oneCheck:{
            type: Boolean,
            required: true
          },
          //控制弹窗头部标题显示隐藏(默认显示)
          closeShow:{
            type: Boolean,
            required: true
          },
        },
        processName:'',//显示文本
        processIDs:'',//查询条件

        processList:[],//流程选中数据

        processCategoryName:'',//流程分类名
        processCategoryIDs:'',//流程分类id
        processCategoryList:[],//流程分类选中数据


        isShow: true,
        arrt: '收起',
        icon: 'el-icon-arrow-up',
        selectValue: '',
        OrgId: '',
        tableData: [],
        isShowtwo: true,
        arrtTwo: '收起',
        iconTwo: 'el-icon-arrow-up',
        showdialog: false,
        showList: false, //列表显示状态
        id:'',
        // 加载动画
        imgLoading: false
      }
    },
    methods: {
      //查询
      search() {
        this.getList();
      },

      //拉取列表数据
      getList() {
        // loading.startLoading();
        if (this.processIDs.length === 0 && this.processCategoryIDs.length === 0) {
          this.$message.warning('请选择流程分类或流程选择进行查询！');
          return;
        }
        this.imgLoading = true;
        let data = {
          processIDs:this.processIDs,
          processCategoryIDs:this.processCategoryIDs,
          "orderBy":''
        };
        this.$axios.post('/api/GetProcessesReport/page', data).then((result) => {
          this.tableData = result.data.processesReportList;
          // loading.endLoading();
          this.imgLoading = false;
        }).catch((err) => {
          // loading.endLoading();
          this.imgLoading = false;
          this.$message.error(`查询列表数据失败!ERROR MESSAGE:${err}`);
        })
      },

      //点击收起展开
      showToggle() {
        this.isShow = !this.isShow;
        if (this.isShow) {
          this.arrt = "收起";
          this.icon = "el-icon-arrow-up"
        } else {
          this.arrt = "展开";
          this.icon = "el-icon-arrow-down"
        }
      },
      showToggleTwo() {
        this.isShowtwo = !this.isShowtwo;
        if (this.isShowtwo) {
          this.arrtTwo = "收起";
          this.iconTwo = "el-icon-arrow-up"
        } else {
          this.arrtTwo = "展开";
          this.iconTwo = "el-icon-arrow-down"
        }
      },

      //导出
      //导出点击触发
      exportList() {
         loading.startLoading({
          lock: true,
          text: '数据量过大，正在拼命加载中...',
          background: 'rgba(255, 255, 255, 0.9)',
          target: document.querySelector('.loadingtext')
        });
        let obj={
          processIDs:this.processIDs,
          processCategoryIDs:this.processCategoryIDs,
          "orderBy": ''
        };
        this.$axios.post('/api/exportExcelForTable', {'tableName':'静态-统计有效流程模板数量','params':obj}, {responseType: 'blob'}).then((result) => {
          loading.endLoading();
          const content = result.data;
          const dowLoadFileName = '静态-统计有效流程模板数量';
          util.exportSearchList(dowLoadFileName, content)

        }).catch((err) => {
          loading.endLoading();
          this.$message.error(`导出失败!ERROR MESSAGE:${err}`);
        })
      },

      //-------------------------------      弹窗
      /**
       * 打开弹出框组件
       */
      openDailog(type) {
        //控制弹窗头部标题显示隐藏(默认显示)
        this.componentParam.closeShow = false;
        // 弹出框类型
        this.componentParam.componentType = type;
        //是否单选
        this.componentParam.oneCheck = false;
        //设置回填数据
        if(type == 'processClassification'){
          this.componentParam.backfillData = this.processCategoryList;
        }else  if(type == 'processSelection'){
          this.componentParam.backfillData = this.processList;
        }
        this.showdialog = true;
        util.showDailog_mask();//解决ie浏览器问题显示遮罩
      },
      /**
       * 关闭弹出框组件
       */
      hideComponent() {
        this.showdialog = false;
        util.hiddenDailog_mask();//解决ie浏览器问题 隐藏遮罩
      },
      /**
       * 弹窗组件带出数据赋值给主组件
       */
      showMsgfromchild(data) {
        //流程分类 选择
        if (data.type && data.type == "processClassification") {
          this.processCategoryList= data.list;
          let  processCategoryIDs = [];
          let  processCategoryName = [];
          for(let node of data.list){
            processCategoryName.push(node.name);
            processCategoryIDs.push(node.id+'');
          }
          this.processCategoryIDs = processCategoryIDs.join(',');
          this.processCategoryName = processCategoryName.join('；');
        }else if(data.type && data.type == "processSelection"){
          //流程 选择
          this.processList = data.list;
          let processId = [];
          let processName = [];
          for(let node of data.list){
            processName.push(node.name);
            processId.push(node.processId+'');
          }
          this.processIDs = processId.join(',');
          this.processName = processName.join('；');
        }
        this.hideComponent();
      },
      //-------------------------------      弹窗

      reset() {
        this.componentParam.backfillData = new Array();
        this.processList = new Array();
        this.processCategoryList = new Array();
        this.$nextTick(() => {
          this.model.companys = '';
          this.id ='';
          this.processName='';
          this.processIDs='';
          this.processCategoryName='';
          this.processCategoryIDs='';
        })
      }
    },
    computed: {},

    watch: {
      fullHeight() {
        return document.documentElement.clientHeight;
      }
    }
  }
</script>

<style lang="scss" scoped>
.table {
  margin-bottom:30px;
}
.el-icon-arrow-down{
  color: #1fa0fd;
  font-size: 14px;
  font-weight: bold;
}
.mz{
  font-size: 16px;
  color: #414952;
}
.mz:hover{
  color: #1fa0fd;
  cursor: pointer;
}
.inputsuhu{
  position: relative;
  margin-right: 35px;
}
.icon-organization{
  position: absolute;
  top: 0;
  right: 2%;
  z-index: 1;
}
.staticReports .staticReports-middle{
  margin-bottom: 0px;
}
.staticReports-middle{
  margin-top: 15px;
}
.staticReports .el-header .staticReports{
  padding: 0px;
  .staticReports-header{
    height: 50px;
    line-height: 50px;
    padding: 0 23px;
    .statisticsMiddle{
      text-align: right;
    }
  }
}
</style>



